import { NgModule } from '@angular/core';
import { DataMapViewerComponent } from './dataMapViewer/dataMapViewer.component';
import { Routes, RouterModule } from '@angular/router';

const routes: Routes = [
    { path: '', component: DataMapViewerComponent },
];

@NgModule({
    imports: [
        RouterModule.forChild(routes)
    ],
    declarations: [DataMapViewerComponent],
    exports: [
        RouterModule
    ]
})
export class DataMapViewerModule { }



